.label {
    display: flex;
    align-items: center;
    cursor: pointer;
}

.checkbox {
    appearance: none;
    width: 20px;
    height: 20px;
    margin-right: 10px;
    background: #b3bdb3;
}

.checkbox:checked {
    appearance: none;
    background-image: url('../img/check.png');
    background-size: 100%;
}

.checkbox:focus {
    outline: #995599 solid 1px;
}
