.button a {
    text-decoration: none;
    margin: 5px;
    font-weight: bold;
}

.activeLink {
    color: red;
}

.header {
    display: flex;
    width: 350px;
    transition-duration: 5s;
    transform: translate(-314px);
    padding: 10px;
}

.header:hover {
    transition-duration: 5000ms;
    transform: translate(0);
}

.button {
    margin: 5px;
    padding: 7px;
    border-radius: 10px;
    background: rgb(100, 100, 100) radial-gradient(circle at 0 0, rgba(255, 255, 255, .65), rgba(255, 255, 255, .35));
    box-shadow: inset rgba(0, 0, 0, .5) -3px -3px 8px, inset rgba(255, 255, 255, .9) 3px 3px 8px, rgba(0, 0, 0, .8) 3px 3px 8px -3px;
}

.button:active {
    color: red;
    padding: 7px;
    border-radius: 8px;
    background: rgb(100, 100, 100) radial-gradient(circle at 0 0, rgba(255, 255, 255, .65), rgba(255, 255, 255, .35));
}