    background-size: 385px;
    background-repeat: no-repeat;
    height: 500px;
    position: relative;
    z-index: 2;
}

.over {
    background: rgba(0, 0, 0, 0.4);
    position: absolute;
    left: 0;
    right: 0;
    top: -87px;
    bottom: 0;
}

.error404 {
    text-align: center;
    font-size: 10em;
    color: #fcf9f9;
    position: relative;
    z-index: 2;
}

.notfound {
    text-align: center;
    color: #fff;
    font-size: 2em;
    position: relative;
    z-index: 2;
    padding-bottom: 155px;
}

.link {
    text-decoration: none;
    position: relative;
    z-index: 2;
    color: #fff;
    padding: 20px;
    font-size: 30px;
}

.link:hover {
    color: #0b33d2;
